
import { Con, Row, Col } from 'bee-layout';
import { Panel } from 'bee-panel';
import Button from 'bee-button';
import React, { Component } from 'react';
import ReactDOM from 'react-dom';


const CARET = <i className="uf uf-arrow-down"></i>;

const CARETUP = <i className="uf uf-arrow-up"></i>;


var Demo1 = require("./demolist/Demo1");var Demo2 = require("./demolist/Demo2");var Demo3 = require("./demolist/Demo3");var DemoArray = [{"example":<Demo1 />,"title":" input","code":"/**\n *\n * @title input\n * @description FormItem\n */\nimport React, { Component } from 'react';\nimport { Form, Label, FormControl } from 'tinper-bee';\nconst FormItem = Form.FormItem;\n\n\nclass Demo1 extends Component {\n    constructor(props) {\n        super(props);\n        this.state = {\n            value: ''\n        }\n    }\n    render() {\n        const self=this;\n        const { getFieldProps, getFieldError } = this.props.form;\n        return (\n                <FormItem className='demo1'>\n                    <Label></Label>\n                    <FormControl placeholder=''\n                     {...getFieldProps('name', {\n                        initialValue:this.state.value,\n                        onChange(value){self.setState({\n                            value\n                        })},\n                        validateTrigger: 'onBlur',\n                        rules: [{\n                            required: true, message: '',\n                        },{\n                            pattern: /[\\u4e00-\\u9fa5]/, message: '',\n                        }],\n                    }) } />\n                    <span className='error'>\n                        {getFieldError('name')}\n                    </span>   \n                </FormItem>\n        )\n    }\n}\n","desc":" FormItem","scss_code":".demo1{\n    font-size: 14px;\n}\n.demo1 .u-label {\n    display: inline;\n}\n.demo1 .u-form-control {\n    width: auto;\n}\n.demo1 .error{\n    font-size: 12px;\n    color: red;\n    margin-left: 10px;\n}"},{"example":<Demo2 />,"title":" form","code":"/**\n *\n * @title form\n * @description \n */\nimport React, {Component} from 'react';\nimport ReactDOM from 'react-dom';\nimport { Form, Button, Label, FormControl } from 'tinper-bee';\nconst FormItem = Form.FormItem;\n\nclass Demo2 extends Component {\n   \n    submit=(e)=>{\n        e.preventDefault();\n        this.props.form.validateFields((err, values) => {\n          if (err) {\n            console.log('', values);\n          }else{\n            console.log('',values)\n          }\n        });\n    }\n    render() {\n        const { getFieldProps, getFieldError } = this.props.form;\n        return (\n            <div className=\"demo2\">\n                <Form >\n                    <FormItem>\n                    <Label></Label>\n                        <FormControl placeholder=\"\" \n                         {...getFieldProps('username', {\n                            validateTrigger: 'onBlur',\n                            rules: [{\n                                required: true, message: '',\n                            }],\n                        }) } \n                        />\n                        <span className='error'>\n                        {getFieldError('username')}\n                    </span>   \n                    </FormItem>\n                    <FormItem>\n                    <Label></Label>\n                        <FormControl placeholder=\"\" type='password'\n                         {...getFieldProps('password', {\n                            validateTrigger: 'onBlur',\n                            rules: [{\n                                required: true, message: '',\n                            }],\n                        }) } \n                        />\n                        <span className='error'>\n                        {getFieldError('password')}\n                    </span>   \n                    </FormItem>\n                    <div className='submit'>\n                    <Button colors=\"primary\" className=\"login\" onClick={this.submit}></Button>\n                    <Button shape=\"border\" className=\"reset\"></Button>\n                    </div>\n                    \n                </Form>\n            </div>\n        )\n    }\n}\n","desc":" ","scss_code":".demo2{\n    font-size: 14px;\n}\n.demo2 .u-label {\n    display: inline-block;\n    min-width: 80px;\n    text-align: right;\n}\n.demo2 .u-form-control {\n    width: auto;\n}\n.demo2 .error{\n    font-size: 12px;\n    color: red;\n    margin-left: 10px;\n}\n.demo2 .submit{\n    padding-left: 90px;\n}\n.demo2 .submit .login{\n    margin-right: 10px;\n}"},{"example":<Demo3 />,"title":" form","code":"/**\n *\n * @title form\n * @description \n */\nimport React, { Component } from 'react';\nimport { Form, Label, Checkbox, Button, FormControl } from 'tinper-bee';\nconst FormItem = Form.FormItem;\n\nclass Demo3 extends Component {\n    constructor(props) {\n        super(props);\n        this.state = {\n            checkbox: false\n        }\n    }\n    submit = (e) => {\n        e.preventDefault();\n        this.props.form.validateFields((err, values) => {\n            if (err) {\n                console.log('', values);\n            } else {\n                console.log('', values)\n            }\n        });\n    }\n    handleConfirmPassword = (rule, value, callback) => {\n        const { getFieldValue } = this.props.form;\n        if (value && value !== getFieldValue('password')) {\n            callback('')\n        }\n        callback();\n    }\n    render() {\n        const self = this;\n        const { getFieldProps, getFieldError } = this.props.form;\n        return (\n            <div className=\"demo3\">\n                <Form>\n                    <FormItem>\n                        <Label></Label>\n                        <FormControl placeholder=\"(8-15)\"\n                            {...getFieldProps('username', {\n                                validateTrigger: 'onBlur',\n                                rules: [{\n                                    required: true, message: '',\n                                }, {\n                                    pattern: /^(?!\\d+$)(?![a-zA-Z]+$)[a-zA-Z0-9]{8,15}$/, message: '',\n                                }],\n                            }) }\n                        />\n                        <span className='error'>\n                            {getFieldError('username')}\n                        </span>\n                    </FormItem>\n\n                    <FormItem>\n                        <Label></Label>\n                        <FormControl placeholder=\"\"\n                            {...getFieldProps('password', {\n                                validateTrigger: 'onBlur',\n                                rules: [{\n                                    required: true, message: '',\n                                }],\n                            }) }\n                        />\n                        <span className='error'>\n                            {getFieldError('password')}\n                        </span>\n                    </FormItem>\n\n                    <FormItem>\n                        <Label></Label>\n                        <FormControl placeholder=\"\"\n                            {...getFieldProps('password2', {\n                                validateTrigger: 'onBlur',\n                                rules: [{\n                                    required: true, message: '',\n                                }, {\n                                    validator: this.handleConfirmPassword\n                                }],\n                            }) }\n                        />\n                        <span className='error'>\n                            {getFieldError('password2')}\n                        </span>\n                    </FormItem>\n\n                    <FormItem>\n                        <Label></Label>\n                        <FormControl placeholder=\"\"\n                            {...getFieldProps('email', {\n                                validateTrigger: 'onBlur',\n                                rules: [{\n                                    required: true, message: '',\n                                }, {\n                                    type: 'email', message: ''\n                                }],\n                            }) }\n                        />\n                        <span className='error'>\n                            {getFieldError('email')}\n                        </span>\n                    </FormItem>\n                    <FormItem>\n                        <Label></Label>\n\n                        <FormControl placeholder=\"\"\n                            {...getFieldProps('phone', {\n                                validateTrigger: 'onBlur',\n                                rules: [{\n                                    required: true, message: '',\n                                }, {\n                                    pattern: /^\\d{11}$/, message: ''\n                                }],\n                            }) }\n                        />\n\n                        <span className='error'>\n                            {getFieldError('phone')}\n                        </span>\n                    </FormItem>\n\n                    <FormItem style={{'paddingLeft':'110px'}}>\n                        <Checkbox colors=\"info\"\n                            defaultChecked={this.state.checkbox}\n                            {\n                            ...getFieldProps('checkbox', {\n                                initialValue: false,\n                            }\n                            ) }\n                        />\n                        <span></span>\n                    </FormItem>\n                    <div className='submit'>\n                        <Button colors=\"primary\" className=\"login\" onClick={this.submit}></Button>\n                        <Button shape=\"border\" className=\"reset\"></Button>\n                    </div>\n                </Form>\n            </div>\n        )\n    }\n}\n","desc":" ","scss_code":".demo3{\n    font-size: 14px;\n}\n.demo3 .u-label {\n    display: inline-block;\n    min-width: 100px;\n    text-align: right;\n}\n.demo3 .u-form-control {\n    width: auto;\n    min-width: 300px;\n}\n.demo3 .error{\n    font-size: 12px;\n    color: red;\n    margin-left: 10px;\n}\n.demo3 .submit{\n    padding-left: 90px;\n}\n.demo3 .submit .login{\n    margin-right: 10px;\n}"}]


class Demo extends Component {
    constructor(props){
        super(props);
        this.state = {
            open: false
        }
        this.handleClick = this.handleClick.bind(this);
    }
    handleClick() {
        this.setState({ open: !this.state.open })
    }

    render () {
        const { title, example, code, desc, scss_code  } = this.props;
        let caret = this.state.open ? CARETUP : CARET;
        let text = this.state.open ? "" : "";

        const header = (
            <div>
                {example}
                <Button style={{"marginTop": "10px"}} shape="block" onClick={ this.handleClick }>
                    { caret }
                    { text }
                </Button>
            </div>
        );
        return (
            <Col md={12} >
                <h3>{ title }</h3>
                <p>{ desc }</p>
                <Panel collapsible headerContent expanded={ this.state.open } colors='bordered' header={ header } footerStyle = {{padding: 0}}>
                    <pre><code className="hljs javascript">{ code }</code></pre>
                    { !!scss_code ? <pre><code className="hljs css">{ scss_code }</code></pre> : null }
                </Panel>
            </Col>
        )
    }
}

class DemoGroup extends Component {
    constructor(props){
        super(props)
    }
    render () {
        return (
                <Row>
                    {DemoArray.map((child,index) => {

                        return (
                            <Demo example= {child.example} title= {child.title} code= {child.code} scss_code= {child.scss_code} desc= {child.desc} key= {index}/>
                        )

                    })}
                </Row>
        )
    }
}

ReactDOM.render(<DemoGroup/>, document.getElementById('tinperBeeDemo'));



